import 'echarts-liquidfill'

import {
  BarChart,
  GaugeChart,
  LineChart,
  PieChart,
  RadarChart,
  ScatterChart
} from 'echarts/charts'
import {
  GridComponent,
  LegendComponent,
  TitleComponent,
  ToolboxComponent,
  TooltipComponent,
  VisualMapComponent
} from 'echarts/components'
import * as echarts from 'echarts/core'
import { UniversalTransition } from 'echarts/features'
import { CanvasRenderer } from 'echarts/renderers'
import ResizeListener from 'element-resize-detector'

echarts.use([
  TooltipComponent,
  LegendComponent,
  GridComponent,
  TitleComponent,
  ToolboxComponent,
  VisualMapComponent,
  PieChart,
  BarChart,
  LineChart,
  ScatterChart,
  RadarChart,
  GaugeChart,
  UniversalTransition,
  CanvasRenderer
])

const props = {
  series: {
    type: Array,
    required: true,
    default: () => []
  },
  options: {
    type: Object,
    default: () => ({})
  }
}

export default defineComponent({
  name: 'Chart',
  props,
  emits: ['on-chart-click'],
  setup(props, { emit }) {
    const myChart = ref<HTMLDivElement>()
    let chart: echarts.ECharts

    onMounted(() => {
      chart = echarts.init(myChart.value as HTMLDivElement)
      updateChartView()
      window.addEventListener('resize', handleWindowResize)
      addChartResizeListener()

      chart.on('click', (param) => emit('on-chart-click', param))
    })

    onScopeDispose(() => {
      window.removeEventListener('resize', handleWindowResize)
    })

    /**
     * echart
     */
    const updateChartView = () => {
      if (!chart) return

      const options = mergeDataToOption()
      chart.setOption(options, true)
    }

    /**
     * echart
     */
    const handleWindowResize = () => {
      chart && chart.resize()
    }

    /**
     * 
     * @returns {Object} echart
     */
    const mergeDataToOption = () =>
      Object.assign(props.options, { series: props.series })

    /**
     * chartechart
     */
    const addChartResizeListener = () => {
      const instance = ResizeListener({
        strategy: 'scroll',
        callOnAdd: true
      })

      instance.listenTo(myChart.value as HTMLDivElement, () => {
        chart && chart.resize()
      })
    }

    //
    watch(
      () => props,
      () => {
        updateChartView()
      },
      { deep: true }
    )

    return () => (
      <div
        ref={myChart}
        style={{
          width: '100%',
          height: '100%'
        }}
      ></div>
    )
  }
})
